$sans-serif: "Open Sans", Helvetica, Arial, sans-serif;
$serif: "PT Serif", Georgia, Times, serif;
$monospace: "Fira Code", monospace, monospace;

// Reset using Normalize
@import "normalize";
@import "config";
@import "modules/contributions";
@import "modules/syntax";
@import "partials/typography";

html, body {
  margin: 0;
  padding: 0;
  background-color: #eee;
}

body {
  padding: 10px;
  font-size: 120%;

  max-width: 990px;
  margin: 0 auto;

  a {
    color: $link-color;

    &:hover {
      color: darken($link-color, 15%);
    }
  }

  article {
    h1 {
      color: #444;
      font-size: 120%;

      .path {
        color: #888;
      }
    }
  }

  div.highlight {
    padding: 20px 10px;
    background: #ffffff;
  }

  footer {
    #comments {
      color: #586069;
      font-size: 120%;
      margin: 0;
      margin-bottom: 1rem;
      padding: 0;
    }

    .contributions .utterances {
      margin: 0;
    }
  }
}

@media only screen and (max-width: 800px) {
  body {
    font-size: 100%;
  }
}
