footer {
  .contributions {
		padding: 1rem 0;

		.note {
			font-size: 14px;
			border: 1px solid #e1e4e8;
			background-color: #f6f8fa;
			border-radius: 3px;
			padding: 16px;
			font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;

			.extra {
				color: #586069;
			}
		}

		#disqus_thread {
			max-width: 90%;
			margin: 0 auto;
		}

		h2, h3 {
			margin-top: 0;
			color: #A66C75;
		}
	
		#share {
			border-bottom: 1px solid #C69AA1;
		}
	
		div.isso-comment div.text {
			font-size: 90%;
		}
	
		div.isso-postbox .textarea, .notification-section {
			font-size: 90%;
		}
	
		.notification-section input {
				margin-right: 5px;
		}
	
		.avatar img {
				max-width: 48px;
				max-height: 48px;
		}

		input, textarea {
			font-size: 90%;
		}
	}
}